@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);

* {
  border: 0;
  box-sizing: border-box;
  font-family: Lato , sans-serif;
  margin: 0;
  padding: 0;
}

.App-logo {
  pointer-events: none;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

.center {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 25px 0;
}

.spinner {
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
  border: 8px solid rgba(0, 0, 0, 0.1);
  border-left-color: #328cc1;
  border-radius: 50%;
  height: 200px;
  width: 200px;
}

@-webkit-keyframes spin {

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.login-container {
  background-color: #0b3c5d;
  color: #d9b310;
  min-height: 100vh;
}

.header-login {
  display: flex;
  height: 35vmin;
  justify-content: center;
  padding: 15px 0;
  width: 100%;
}

.header-login img {
  width: 50%;
}

.container-form-login {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 30vh;
  min-width: 30vw;
  padding: 25px 0;
}

.card-login {
  background-color: white;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  min-height: 50vh;
  min-width: 30vw;
  padding: 1rem;
}

.input-field-login {
  border: 2px solid #328cc1;
  border-radius: 8px;
  color: #1d2731;
  font-size: 1rem;
  margin: 10px 0;
  outline: none;
  padding: 15px 10px;
}

.button-login {
  background-color: #0b3c5d;
  border: none;
  color: #d9b310;
  cursor: pointer;
  font-size: 1rem;
  padding: 15px 10px;
  width: 100%;
}

.button-login:hover {
  color: #328cc1;
}

.button-login:disabled {
  background-color: #d9d9d9;
  color: #1d2731;
  cursor: auto;
}

.settings-button {
  background-color: #0b3c5d;
  color: #d9b310;
  cursor: pointer;
  font-size: 1rem;
  padding: 15px 10px;
  width: 100%;
}

.settings-button:hover {
  color: #328cc1;
}

.hr-login {
  border: 3px #1d2731 solid;
  border-radius: 10px;
  margin: 15px 0;
}

.header-feedback {
  align-items: center;
  background-color: #0b3c5d;
  color: #d9b310;
  display: flex;
  flex-direction: row;
  font-style: italic;
  justify-content: space-around;
  min-height: 15vh;
  width: 100vw;
}

.header-feedback span {
  padding: 0 10px;
}

.container-feedback {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 5rem 0;
}

.feedback-title {
  color: #1d2731;
  padding: 0 0 30px;
}

.feedback-scoreboard {
  color: #1d2731;
  text-align: center;
}

.feedback-scoreboard span {
  padding: 0 5px;
}

.feedback-scoreboard-button {
  margin: 30px 0;
}

.feedback-buttons {
  background-color: #0b3c5d;
  border: 2px solid #d9b310;
  color: #d9b310;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 10px;
  width: 40vw;
}

.feedback-buttons:hover {
  color: #328cc1;
}

.header-quiz {
  align-items: center;
  background-color: #0b3c5d;
  color: #d9b310;
  display: flex;
  flex-direction: row;
  font-style: italic;
  justify-content: space-around;
  min-height: 15vh;
  width: 100vw;
}

.header-quiz span {
  padding: 0 10px;
}

.container-quiz {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 4rem 0;
}

.card-question-quiz {
  max-width: 100%;
}

.info-question-quiz {
  font-size: 1.6rem;
  text-align: center;
}

.list-buttons-quiz {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  padding: 15px 0;
}

.item-list-buttons-quiz {
  padding: 15px;
}

.li-quiz-button {
  border: 3px solid #328cc1;
  color: #1d2731;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 10px;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  width: 40vw;
}

.li-quiz-button:hover {
  color: #328cc1;
  padding: 12px;
  width: 40.5vw;
}

.li-quiz-button:disabled {
  color: #1d2731;
  cursor: auto;
}

.correct-answer {
  border: 3px solid rgb(6, 240, 15);
}

.wrong-answer {
  border: 3px solid red;
}

.container-button-next {
  display: flex;
  justify-content: center;
}

.button-next {
  background-color: #0b3c5d;
  border: 1px solid #0b3c5d;
  color: #d9b310;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 10px;
  width: 40vw;
}

.button-next:hover {
  color: #328cc1;
}

.timer {
  align-items: center;
  border: 3px solid #1d2731;
  border-radius: 50%;
  display: flex;
  height: 64px;
  justify-content: center;
  width: 64px;
}

.header-ranking {
  align-items: center;
  background-color: #0b3c5d;
  color: #d9b310;
  display: flex;
  flex-direction: row;
  font-style: italic;
  justify-content: center;
  min-height: 15vh;
  width: 100vw;
}

.ranking-table {
  border-collapse: collapse;
  width: 100vw;
}

.ranking-thead {
  background-color: #1d2731;
  color: black;
  font-size: 1.6rem;
  font-weight: 900;
}

.ranking-thead th {
  color: #d9b310;
  padding: 20px 0;
}

.ranking-tbody {
  font-size: 1.3rem;
  font-weight: 400;
  height: 100%;
  text-align: center;
}

.ranking-tbody td {
  font-size: 1.2rem;
  padding: 10px;
}

.td-flex {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.td-flex img {
  border: 3px solid #d9b310;
  border-radius: 50%;
  margin: 0 15px;
  width: 56px;
}

.indexPair {
  background-color: #328cc1;
  color: #1d2731;
}

.indexOdd {
  background-color: #1d2731;
  color: #d9b310;
}

.ranking-button {
  display: flex;
  justify-content: center;
  margin-top: 90px;
}

